@import url('https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap');

.login{
    background-color: #4c3f54;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-decoration: none;
    font-family: 'Zen Dots', cursive;
    font-size: 16px;
}

#LogoImage {
    width: 90px;
    height: 90px;
    display: block;
    cursor: pointer;
    margin-right:auto;
}

/*Button*/
.ButtonContainer {
    text-align: center;
    margin: 10rem;
}

.login  button {
    margin: 20px;
    padding: 16px 50px;
    border: 3px solid;
    background-color: rgb(0, 255, 221);
    color: black;
    display: inline-block;
    font-family: 'Zen Dots', cursive;
    text-transform: uppercase;
    border-radius: 10px;
    transition: 0.5s;
}

.login  button:hover {
    background-color: rgb(0, 150, 250); /* Green */
    color: white;
    transition: 0.5s;
}

.login  div > img{
    object-fit: contain;
    height: 150px;
}

/*Footer code*/

.FooterContainer {
    background-color: #686f8d;
    position: relative;
    margin-top: -100px;
    clear: both;
    padding: 2rem 0 1rem;
    margin: 6rem auto 0;
    box-sizing: border-box;
    display: block;
    bottom: 0;
}

.Footer {
    width: 80%;
    height: 15vh;
    background-color: #686f8d;
    color: white;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 auto;
    bottom: 0;
}

.FooterHeading {
    display: flex;
    flex-direction: column;
    margin-right: 2rem;
}

.FooterHeading h2 {
    margin-bottom: 1rem;
    font-size: 1.4em;
    color: rgb(90, 69, 212);
    font-weight: bold;
}

.FooterHeading a {
    color: white;
    text-decoration: none;
    margin-bottom: 0.5rem;
}

.FooterHeading a:hover {
    color: #415ee2;
    transition: 0.3s ease-out;
}

/*Nav Menu configuration*/
header li, header a, header Button{
    font-weight: 800;
    font-size: 1em;
    text-decoration: none;
    color: white;
}

header{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.headerDiv{
    display: block;
    margin-right:auto;
}


.NavMenu{
    display: inline-block;
    margin-right: 20px;
    list-style: none;
}

.NavMenu li {
    display: inline-block;
    padding: 0px 20px;
}

.NavMenu li a {
    transition: all 0.5s ease 0s;
}

.NavMenu li a:hover {
    color: #6072c4;
}

/*ScrollBar styling*/
/* Works on Chrome, Edge, and Safari */
body::-webkit-scrollbar {
    width: 12px;               /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
    background: rgb(105, 133, 143);        /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
    background-color: rgb(51, 63, 68);    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
    border: 3px;  /* creates padding around scroll thumb */
}

/* Works on Firefox */
body {
    scrollbar-width: thin;
    scrollbar-color: rgb(51, 63, 68) rgb(105, 133, 143);
}

/*Style for different dimensions*/
@media screen and (max-width: 802px) {
    .Footer {
        padding-top: 2rem;
        height: 18vh;
    }

    .FooterHeading h2 {
        font-size: 1.2em;
    }

    .FooterHeading a {
        font-size: 0.9em;
    }
}

@media screen and (max-width: 690px) {
    .Footer {
        padding-top: 2rem;
        height: 20vh;
    }

    .FooterHeading{
        width:25%;
    }

    .FooterHeading h2 {
        font-size: 1.2em;
    }

    .FooterHeading a {
        font-size: 0.9em;
    }
}

@media screen and (max-width: 560px) {
    #NavButton{
