@import url(https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap);
*{
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    display: -webkit-flex;
    display: flex;
}
.chat {
display: -webkit-flex;
display: flex;
-webkit-flex-direction: column;
        flex-direction: column;
-webkit-flex: 0.75 1;
        flex: 0.75 1;
background-color: #363a3f;
height: 100vh;
}

.chat__messages {
-webkit-flex: 1 1;
        flex: 1 1;
overflow-y: scroll
}
.chat__input {
color: lightgray;
display: -webkit-flex;
display: flex;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: center;
        align-items: center;
padding: 15px;
border-radius: 5px;
margin: 20px;
border-top: 1px solid gray;
background-color: #474053;
}

.chat__input > form > input {
padding: 15px;
background: transparent;
border: none;
width: 100%;
outline-width: 0;
color: white;
font-size: large;
}

.chat__input > form {
-webkit-flex: 1 1;
        flex: 1 1;
}

.chat__inputButton {
display: none;
}

.chat__inputIcons > .MuiSvgIcon-root {
padding: 5px;
}
.chatHeader {
display: -webkit-flex;
display: flex;
-webkit-justify-content: space-between;
        justify-content: space-between;
-webkit-align-items: center;
        align-items: center;
color: gray;
padding: 10px;
}

.chatHeader__hash {
color: gray;
font-size: 30px;
padding: 10px;
}

.chatHeader__left > h3 {
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
color: white;
-webkit-flex: 0.5 1;
        flex: 0.5 1;
-webkit-justify-content: space-between;
        justify-content: space-between;
}
.chatHeader__right {
display: -webkit-flex;
display: flex;
}

.chatHeader__right > .MuiSvgIcon-root {
padding: 5px;
cursor: pointer;
}

.chatHeader__right > .MuiSvgIcon-root:hover {
color: white;
}

.chatHeader__search {
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
color: gray;
background-color: #2f3135;
border-radius: 3px;
padding: 3px;
}

.chatHeader__search > input {
background: transparent;
outline-width: 0;
color: white;
border: none;
}
.message{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 20px;
    color: white;
}

.message__info{
    margin-left: 20px;
}

.message__timestamp{
    color: gray;
    margin-left: 20px;
    font-size: x-small;
}
.sidebar {
display: -webkit-flex;
display: flex;
-webkit-flex-direction: column;
        flex-direction: column;
-webkit-flex: 0.25 1;
        flex: 0.25 1;
height: 100vh;
background-color: #bac3d4;
}

.sidebar__top {
display: -webkit-flex;
display: flex;
-webkit-justify-content: space-between;
        justify-content: space-between;
-webkit-align-items: center;
        align-items: center;
padding: 20px;
background-color: #7b7d81;
color: rgb(0, 0, 0);
}

.sidebar__channels {
-webkit-flex: 1 1;
        flex: 1 1;
cursor: pointer;
}
.sidebar__channels:hover {
color: rgb(0, 0, 0);
}

.sidebar__channelsHeader {
display: -webkit-flex;
display: flex;
-webkit-justify-content: space-between;
        justify-content: space-between;
-webkit-align-items: center;
        align-items: center;
padding: 10px;
background-color: #bac3d4;
color: black;
}

.sidebar__header {
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
}

.sidebar__profile {
display: -webkit-flex;
display: flex;
-webkit-justify-content: space-between;
        justify-content: space-between;
-webkit-align-items: center;
        align-items: center;
color: black;
background-color: #bac3d4;
padding: 10px;
border-top: 1px solid black;
}

.sidebar__profileInfo {
-webkit-flex: 1 1;
        flex: 1 1;
padding: 10px;
}

.sidebar__profileInfo > h3 {
color: black;
}

.sidebar__profileIcons > .MuiSvgIcon-root {
padding: 10px;
}
.sidebarChannel {
display: -webkit-flex;
display: flex;
padding-left: 15px;
-webkit-align-items: center;
        align-items: center;
background-color: #2f3135;
color: gray;
cursor: pointer;
}
.sidebarChannel:hover > h4 {
color: white;
background-color: #40464b;
}

.sidebarChannel_hash {
font-size: 30px;
padding: 8px;
}
.login{
    background-color: #4c3f54;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-decoration: none;
    font-family: 'Zen Dots', cursive;
    font-size: 16px;
}

#LogoImage {
    width: 90px;
    height: 90px;
    display: block;
    cursor: pointer;
    margin-right:auto;
}

/*Button*/
.ButtonContainer {
    text-align: center;
    margin: 10rem;
}

.login  button {
    margin: 20px;
    padding: 16px 50px;
    border: 3px solid;
    background-color: rgb(0, 255, 221);
    color: black;
    display: inline-block;
    font-family: 'Zen Dots', cursive;
    text-transform: uppercase;
    border-radius: 10px;
    transition: 0.5s;
}

.login  button:hover {
    background-color: rgb(0, 150, 250); /* Green */
    color: white;
    transition: 0.5s;
}

.login  div > img{
    object-fit: contain;
    height: 150px;
}

/*Footer code*/

.FooterContainer {
    background-color: #686f8d;
    position: relative;
    margin-top: -100px;
    clear: both;
    padding: 2rem 0 1rem;
    margin: 6rem auto 0;
    box-sizing: border-box;
    display: block;
    bottom: 0;
}

.Footer {
    width: 80%;
    height: 15vh;
    background-color: #686f8d;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0 auto;
    bottom: 0;
}

.FooterHeading {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-right: 2rem;
}

.FooterHeading h2 {
    margin-bottom: 1rem;
    font-size: 1.4em;
    color: rgb(90, 69, 212);
    font-weight: bold;
}

.FooterHeading a {
    color: white;
    text-decoration: none;
    margin-bottom: 0.5rem;
}

.FooterHeading a:hover {
    color: #415ee2;
    transition: 0.3s ease-out;
}

/*Nav Menu configuration*/
header li, header a, header Button{
    font-weight: 800;
    font-size: 1em;
    text-decoration: none;
    color: white;
}

header{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.headerDiv{
    display: block;
    margin-right:auto;
}


.NavMenu{
    display: inline-block;
    margin-right: 20px;
    list-style: none;
}

.NavMenu li {
    display: inline-block;
    padding: 0px 20px;
}

.NavMenu li a {
    transition: all 0.5s ease 0s;
}

.NavMenu li a:hover {
    color: #6072c4;
}

/*ScrollBar styling*/
/* Works on Chrome, Edge, and Safari */
body::-webkit-scrollbar {
    width: 12px;               /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
    background: rgb(105, 133, 143);        /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
    background-color: rgb(51, 63, 68);    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
    border: 3px;  /* creates padding around scroll thumb */
}

/* Works on Firefox */
body {
    scrollbar-width: thin;
    scrollbar-color: rgb(51, 63, 68) rgb(105, 133, 143);
}

/*Style for different dimensions*/
@media screen and (max-width: 802px) {
    .Footer {
        padding-top: 2rem;
        height: 18vh;
    }

    .FooterHeading h2 {
        font-size: 1.2em;
    }

    .FooterHeading a {
        font-size: 0.9em;
    }
}

@media screen and (max-width: 690px) {
    .Footer {
        padding-top: 2rem;
        height: 20vh;
    }

    .FooterHeading{
        width:25%;
    }

    .FooterHeading h2 {
        font-size: 1.2em;
    }

    .FooterHeading a {
        font-size: 0.9em;
    }
}

@media screen and (max-width: 560px) {
